.footer_outer_container {
background-color: #111;

display: flex;
justify-content: center;
/* background-color: rgb(137, 6, 6); */
}
.footer_inner_container {
    width: 60%;
    color: grey;
}
.footer_icons {
    width: 12%;
    display: flex;
    justify-content: space-around;
    margin-left: 10px;
    color: white;
}
.footer_data {
    display: flex;
    justify-content: space-between;
}
.footer_data ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 12vh;
}
.footer_link{
list-style: none;
color: grey;
 text-decoration: none; /* Remove default underline */
  transition: text-decoration 0.3s; 
}
.footer_link:hover {
  text-decoration: underline; 
  color: grey;

}
.footer_data ul li{
    cursor: pointer;
}
.service_code{
    display: flex;
    align-items: center;
    justify-content: center;
    border: white 1px solid;
    width: 10%;
    margin: 10px 0;
    cursor: pointer;

}
.copy-right{
    font-size: 12px;
    margin-bottom: 5%;
  
}